@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,100&display=swap);
body{background-color:#4492E8}#root{max-width:300px;min-height:500px;max-height:500px;background-color:#f7f7f7;margin:20px auto;padding:20px;border-radius:10px;box-shadow:5px 5px 10px rgba(0,0,0,0.3);font-family:'Lato', sans-serif;box-sizing:border-box;position:relative;overflow:hidden}h1{font-size:16pt;font-weight:bold;text-align:center}.search{width:90%;margin:auto;height:25px;text-align:center;padding:5px 10px;border:none;box-shadow:3px 3px 6px rgba(0,0,0,0.2);border-radius:30px}.todoList ul{padding:0px;width:100%;max-height:300px;display:block;overflow-y:scroll}.todoList ul li.completed div{color:#d2d2d2}.todoList ul li{list-style-type:none;display:flex;flex-direction:row;justify-content:space-between;box-shadow:3px 3px 6px rgba(0,0,0,0.2);border-radius:20px;margin-top:20px;padding:0px 20px;position:relative;width:80%}.todoList ul li div{display:flex;flex-direction:row;align-items:center;font-size:16px;color:#6e6e6e}.todoList ul li div input{background:#24D8A8}.todoList ul li button{position:absolute;top:-10px;right:-10px;width:30px;height:30px;border-radius:15px;padding:0px;display:flex;justify-content:center;align-items:center;border:none;background:#d44f4f;color:#fff}.createTodo{position:absolute;width:40px;height:40px;border-radius:20px;background-color:#61A1D2;color:#fff;border:none;left:calc(50% - 20px);bottom:10px}.modal{background:rgba(32,35,41,0.8);position:fixed;top:-10px;left:-10px;right:-10px;bottom:-10px;display:flex;justify-content:center;align-items:center;color:white}.clouse{z-index:999;transform:rotate(45deg)}form{width:90%;max-width:300px;background-color:#fff;padding:33px 40px;display:flex;align-content:center;flex-direction:column}label{text-align:center;font-weight:bold;font-size:22px;margin-bottom:26px;color:black}textarea{background-color:#F9FBFC;border:2px, solid #202329;border-radius:2px;box-shadow:0px 5px 50px rgba(32,35,41,0.25);color:#1E1E1F;font-size:20px;text-align:center;padding:12px;height:96px;width:calc(100%-25px)}textarea:-ms-input-placeholder{color:#a5a5a5;font-family:'Montserrat';font-weight:400}textarea::placeholder{color:#a5a5a5;font-family:'Montserrat';font-weight:400}textarea:focus{outline-color:#61DAFA}.TodoForm-buttonContainer{margin-top:14px;display:flex;justify-content:space-between;align-items:center;width:100%}.TodoForm-button{cursor:pointer;display:inline-block;font-size:20px;color:#202329;font-weight:400;width:120px;height:48px;border-radius:2px;border:none;font-family:'Montserrat'}.TodoForm-button-add{background-color:#61DAFA;box-shadow:0px 5px 25px rgba(97,218,250,0.5)}.TodoForm-button-cancel{background:transparent}

